import React, { Component } from 'react';
import './App.css';
import './Flex.css';
import './Icons.css';
import "react-image-gallery/styles/css/image-gallery.css";
import langEn from './translations/en/en';
import langPt from './translations/pt/pt';
import LangSelect from './components/LangSelect/LangSelect';
import Home from './components/Home/Home';
import Budget from './components/Budget/Budget';
import Footer from './components/Footer/Footer';
import ProjectPage from './components/ProjectPage/ProjectPage';
import AccessBudget from './components/AccessBudget/AccessBudget';
import CV from './components/CV/CV';
import history from './history';
import { AnimatedSwitch, spring } from 'react-router-transition';

import {
  //Router,
  BrowserRouter,
  Route,
  NavLink
} from 'react-router-dom';

// we need to map the `scale` prop we define below
// to the transform style property
function mapStyles(styles) {
  return {
    opacity: styles.opacity,
    transform: `scale(${styles.scale})`,
  };
}

// wrap the `spring` helper to use a bouncy config
function bounce(val) {
  return spring(val, {
    stiffness: 330,
    damping: 22,
  });
}

// child matches will...
const bounceTransition = {
  // start in a transparent, upscaled state
  atEnter: {
    opacity: 0,
    scale: 1.2,
  },
  // leave in a transparent, downscaled state
  atLeave: {
    opacity: bounce(0),
    scale: bounce(0.8),
  },
  // and rest at an opaque, normally-scaled state
  atActive: {
    opacity: bounce(1),
    scale: bounce(1),
  },
};

let prevLocation = {};
history.listen(location => {
  const pathChanged = prevLocation.pathname !== location.pathname;
  const hashChanged = prevLocation.hash !== location.hash;
  if (pathChanged || hashChanged) window.scrollTo(0, 0);
  prevLocation = location;
});

class App extends Component {
  constructor(props){
    super(props);
    this.state = { lang: langPt }; 
    this.renderProjects = this.renderProjects.bind(this);
  }

  changeLang(lang){
    this.setState({lang: (lang === 'pt') ? langPt : langEn})
  }

  renderProjects(props){
    let id = props.match.params.id,
        project = this.state.lang.home
          .projects.data.find((obj)=> (obj.id === id)); 
    return (<ProjectPage 
              footer={this.state.lang.app.footer}    
              data={project}
           />);
  }

  render() {
    this.render
    return (
      <BrowserRouter history={history} basename="/work">
        <div>
          <div className="wrapper">
            <div className="main-body">
              <div 
                className="navbar flex-row inline-end wrap g-pad">
                <ul>
                  <li><NavLink exact to="/">
                        {this.state.lang.app.nav.home}
                      </NavLink></li>
                  <li><NavLink to="/budget">
                        {this.state.lang.app.nav.budget}
                      </NavLink></li>
                  {/*
                  <li><NavLink to="/como-funciona-o-calculo">
                        Dynamic
                      </NavLink></li>
                  */}
                </ul>
                <LangSelect lang={this.state.lang.lang}
                  changeLang={this.changeLang.bind(this)} />
              </div>
              <AnimatedSwitch
                atEnter={bounceTransition.atEnter}
                atLeave={bounceTransition.atLeave}
                atActive={bounceTransition.atActive}
                mapStyles={mapStyles}
                className="route-wrapper"
              >
                <Route exact path="/" 
                  render={ () => 
                    <Home 
                    footer={this.state.lang.app.footer}
                    lang={this.state.lang.home} />
                  }
                />
                <Route path="/budget" 
                  render={ () => 
                    <Budget 
                    footer={this.state.lang.app.footer}
                    lang={this.state.lang.budget} />
                  }
                />
                <Route path="/access-budget" 
                  render={ () => 
                    <AccessBudget 
                    footer={this.state.lang.app.footer}
                    lang={this.state.lang.accessBudget} />
                  }
                />
                <Route path="/skills" 
                  render={ () => 
                    <CV
                    footer={this.state.lang.app.footer}
                    lang={this.state.lang.CV} />
                  }
                />
                <Route path="/projects/:id" 
                  render={this.renderProjects}
                />
                <Route path="/posts/:id" 
                  render={ (props) => 
                    <div>
                      {this.state.lang.posts[props.match.params.id]}
                      <Footer 
                        lang={this.state.lang.app.footer}/>
                    </div>
                  }
                />
              </AnimatedSwitch>
            </div>
          </div>
        </div>
      </BrowserRouter>
    );
  }
}

export default App;



